import { Component, EventEmitter, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-uploader',
  templateUrl: './uploader.component.html',
  styleUrls: ['./uploader.component.css'],
})
export class UploaderComponent implements OnInit {
  uploaded = false;
  uploadResults;
  status;
  root = 'https://drive.orderbot.org';

  @Output() done = new EventEmitter<boolean>();

  constructor() {}

  ngOnInit() {}

  upload(event) {
    this.status = `Uploading...`;
    const inputEl = event.target;
    const files: FileList = inputEl.files;
    if (files.length > 0) {
      const fd = new FormData();
      if (files instanceof FileList) {
        for (let i = 0; i < files.length; i++) {
          fd.append('files', files[i]);
        }
      }
      if (files instanceof Blob) {
        fd.append('files', files);
      }

      const xhr = new XMLHttpRequest();
      xhr.upload.onprogress = _event => {
        if (_event.lengthComputable) {
          const p = (_event.loaded / _event.total) * 100;
          this.status = `Uploading ${p} %`;
        }
      };
      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
          // alert(JSON.parse(xhr.response)[0].path)
          this.status = `Upload complete`;
          this.uploaded = true;
          this.uploadResults = JSON.parse(xhr.response);
          // console.log(this.uploadResults);
          this.uploadResults.map(item => {
            item.url = `${this.root}/${item.path}`;
          });
          this.done.emit(this.uploadResults);
        }
      };
      xhr.withCredentials = true;
      xhr.open('post', `${this.root}/upload`, false, 'asd', 'asd');
      // xhr.setRequestHeader('authorization', 'Basic ' + btoa('asd:asd'));
      xhr.send(fd);
    }
  }
}
