import { Component, OnInit } from '@angular/core';
import FileSaver from 'file-saver';
import { ApiService } from 'src/app/shared/api.service';
import { NotificationService } from 'src/app/shared/notification.service';

@Component({
  selector: 'app-importexport',
  templateUrl: './importexport.component.html',
  styleUrls: ['./importexport.component.css'],
})
export class ImportexportComponent implements OnInit {
  importurl;
  tables = ['Products'];
  // tables = ['Products', 'Categories', 'Inventory', 'Outlets', 'Pages'];
  table = 'Products';

  constructor(
    private api: ApiService,
    private notificationService: NotificationService
  ) {}

  ngOnInit() {}

  async export(form) {
    const csv = (await this.api
      .post(`/api3/exports/${this.table}`, form, { responseType: 'text' })
      .toPromise()) as BlobPart;
    console.log(csv);
    const blob = new Blob([csv], { type: 'text/csv' });
    FileSaver.saveAs(blob, `${this.table} (${Date.now().toString(36)}).csv`);
  }

  uploaded(uploadresults) {
    console.log('uploaded');
    // console.log({ uploadresults });
    this.importurl = uploadresults[0].url;
  }

  async runimport(table, importurl) {
    console.log('runimport');
    console.log(importurl);
    const results = await this.api
      .post(`/api3/imports/`, { table, importurl })
      .toPromise();
    this.notificationService.alert(JSON.stringify(results));
  }
}

